<template>
  <div>
    <CRow>
      <CCol md="12">
        <CCard>
          <CCardHeader>
            Australia covid alert status
          </CCardHeader>
          <CCardBody>
            <CRow>
              <CCol sm="12" lg="6">
                <CRow>
                  <CCol sm="6">
                    <CCallout color="info">
                      <small class="text-muted">Total Alerts</small><br>
                      <strong class="h4">{{this.data_list.total_alert}}</strong>
                    </CCallout>
                  </CCol>
                  <CCol sm="6">
                    <CCallout color="danger">
                      <small class="text-muted">Last Update</small><br>
                      <strong class="h4">{{this.data_list.date}}</strong>
                    </CCallout>
                  </CCol>
                </CRow>
                <hr class="mt-0">
                <div class="progress-group mb-4" v-for="(v, i) in this.data_list.daylist" :key="i">
                  <div class="progress-group-prepend" v-show="typeof v.day === 'string'">
                    <span class="progress-group-text">
                      {{v.day}} ({{v.count}})
                    </span>
                  </div>
                  <div class="progress-group-bars" v-show="typeof v.day === 'string'">
                    <CProgress
                      class="progress-xs"
                      color="info"
                      :value="(v.count/data_list.daylist[Object.keys(data_list.daylist).length-1].sum)*100"
                    />
                  </div>
                </div>
              </CCol>
              <CCol sm="12" lg="6">
                <CRow>
                  <CCol sm="6">
                    <CCallout color="warning">
                      <small class="text-muted">Alert Suburbss</small><br>
                      <strong class="h4">{{this.data_list['list'][(data_list['list']).length-1]}}</strong>
                    </CCallout>
                  </CCol>
                </CRow>
                <hr class="mt-0">
                <ul class="horizontal-bars type-2">
                  <div class="progress-group" v-for="(v, i) in this.data_list['list']" :key="i">
                    <div class="progress-group-header"  v-if="i < data_list['list'].length - 1 ">
                      <fa :icon="['fas', 'location-arrow']" /> 
                      <span class="title"> {{v[0]['Suburb']}} ({{v.length}})</span>
                      <span class="ml-auto font-weight-bold">{{ Math.floor( ( v.length / data_list.total_alert ) * 100)}}%</span>
                    </div>
                    <div class="progress-group-bars" v-if="i < data_list['list'].length - 1 ">
                      <CProgress
                        class="progress-xs"
                        :value="( v.length / data_list.total_alert ) * 100"
                        color="warning"
                      />
                    </div>
                  </div>
                </ul>
              </CCol>
            </CRow>
            <br/>
            <CTabs add-tab-classes="mt-1">
              <CTab v-for="(v, i) in this.data_list['list']" :key="i" :active="i == 0">
                <template slot="title" v-if="i < data_list['list'].length - 1">
                  <CIcon name="cil-globe-alt"/> {{v[0]['Suburb']}} ({{v.length}})
                </template>
                
                <CDataTable
                  class="mb-0 table-outline"
                  hover
                  :items="v"
                  :fields="tableFields"
                  head-color="light"
                  striped
                  pagination
                  v-if="i < data_list['list'].length - 1"
                >
                </CDataTable>
              </CTab>
            </CTabs>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  </div>
</template>

<script>
import MainChartExample from './charts/MainChartExample'

export default {
  name: 'Dashboard',
  components: {
    MainChartExample
  },
  data () {
    return {
      data_list: {
        total_alert: 0,
        date: '2000-01-01',
        list: {},
        daylist: {}
      },
      sorting_list: {},
      selected: 'Month',
      tableFields: [
        { key: 'Date' },
        { key: 'Time' },
        { key: 'Address', label: '', _classes: 'text-center' },
        { key: 'Venue', _classes: 'text-center' },
        { key: 'Alert', label: 'Advice' },
      ]
    }
  },
  created(){
    this.getCurrentLocation()
  },
  methods: {
    async getCurrentLocation(){
      let api_url = "https://data.nsw.gov.au/data/dataset/0a52e6c1-bc0b-48af-8b45-d791a6d8e289/resource/f3a28eed-8c2a-437b-8ac1-2dab3cf760f9/download/covid-case-locations-20210812-1911.json"
      axios.get(api_url)
      .then( (res) =>{
        this.data_list['total_alert'] = res.data.data.monitor.length
        this.data_list['date'] = res.data.date
        this.sortAlert(res.data.data.monitor)
      })
    },
    sortAlert( data ){
      this.sortBySuburb( data )
      this.sortByDate( data )
    },
    sortBySuburb( data ){
      let sorting_result = {}
      let totalSuburb = 0
      for( let i = 0; i < data.length; i ++){
        let current_suburb = data[i]['Suburb']
        if( sorting_result[current_suburb] === undefined ){
          sorting_result[current_suburb] = []
        }
        sorting_result[current_suburb].push(data[i])
      }
      totalSuburb = Object.keys(sorting_result).length
      sorting_result = (Object.values(sorting_result))
      sorting_result.sort( (a, b) => {
        return b.length - a.length
      })
      sorting_result = sorting_result.slice(0, 5)
      this.data_list['list'] = sorting_result
      this.data_list['list'].push(totalSuburb)
    },
    sortByDate( data ){
      let sorting_result = {}
      for( let i = 0; i < data.length; i ++){
        let date = new Date(data[i]['Date'])
        let currentdate = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()
        if( sorting_result[currentdate] === undefined ){
          sorting_result[currentdate] = {
            'day': currentdate,
            'count': 1
          }
        }else{
          sorting_result[currentdate]['count'] += 1
        }
      }
      let sum = 0
      let t = Object.values(sorting_result)
      t.sort( (a, b) => {
        let da = new Date(a.day)
        let db = new Date(b.day)
        return db - da
      })
      t = t.slice(0, 7)
      
      sum = t.reduce( (a, b) => {
        if (typeof a === "object")
          return a.count + b.count
        return a + b.count
      })
      t.push({'sum': sum})
      this.data_list.daylist = t
    },
  }
}
</script>
